<template>
  <div class="layout">
    <div class="sidebar" v-bind:class="{ visible: sidebarVisible }">
      <div class="ui vertical menu">
        <div class="item">
          <a class="ui logo icon image" href="/">
            <img width="140" height="140" src="../assets/knife_256.png" />
          </a>
          <a href="/"><h4>Tools</h4></a>
        </div>

        <div class="item" v-for="menu in menus" :key="menu.key">
          <div class="header">{{ menu.name }}</div>
          <div class="menu">
            <router-link v-bind:to="item.to" v-for="item in menu.items" :key="item.key">
              <a class="item" v-bind:class="{ active: isActive(item.key) }" @click="navigate">{{
                item.name
              }}</a>
            </router-link>
          </div>
        </div>
      </div>
    </div>
    <div class="wrapper">
      <div class="dimmed" v-if="sidebarVisible" @click="toggleSidebar">></div>
      <div class="ui menu mobile-menu">
        <div class="ui container">
          <div class="item" @click="toggleSidebar">
            <i class="bars icon"></i>
          </div>
          <div class="item">
            Tools
          </div>
        </div>
      </div>
      <div class="main">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    menus: Array,
  },
  data() {
    return {
      sidebarVisible: false,
    };
  },
  methods: {
    isActive(name) {
      return name === window.location.hash.substr(2);
    },
    toggleSidebar() {
      this.sidebarVisible = !this.sidebarVisible;
    },
    navigate() {
      this.sidebarVisible = false;
    },
  },
};
</script>

